import React from 'react';
import PropTypes from '@dhis2/prop-types';
import { Line } from 'react-chartjs-2';
import { FAVORITE_VIEWS } from '../../constants/categories.js';
import ChartWrapper from './ChartWrapper.js';
import { getTitles, getLabels, getAverageTotalFavoriteViewsDatasets } from './selectors.js';
import options from './options.js';

const AverageTotalFavoriteViewsChart = ({
  data,
  interval
}) => {
  const {
    title,
    subtitle
  } = getTitles(FAVORITE_VIEWS);
  const labels = getLabels(data, interval);
  const datasets = getAverageTotalFavoriteViewsDatasets(data);
  return React.createElement(ChartWrapper, {
    title: title,
    subtitle: subtitle
  }, React.createElement(Line, {
    data: {
      labels,
      datasets
    },
    options: options
  }));
};

AverageTotalFavoriteViewsChart.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape({
    averageViews: PropTypes.number.isRequired,
    day: PropTypes.number,
    month: PropTypes.number,
    week: PropTypes.number,
    year: PropTypes.number
  })).isRequired,
  interval: PropTypes.string.isRequired
};
export default AverageTotalFavoriteViewsChart;